import SkillsTitle from "./SkillsTitle";

function SkillsParent(props){

    let skills = Object.entries(props.skills);
    let listOfSkills = [];

    for(let i=0; i<skills.length; i++){
        
        let skill = <SkillsTitle title={skills[i][0]} skills={skills[i][1]} />
        listOfSkills.push(skill);
    }

    return listOfSkills;
}

export default SkillsParent;