function SkillsInIndividualField(props) {

    let individualSkillsEntries = Object.entries(props.skills);

    console.log(individualSkillsEntries[0][0]);

    let individualSkills = [];

    for (let i = 0; i < individualSkillsEntries.length; i++) {

        let individualSkill = <div class="row align-items-center">
            <div class="col-12 col-md-3">
                <h5>{individualSkillsEntries[i][0]}</h5>
            </div>
            <div class="col-12 col-md-9">
                <div class="progress">
                    <div class="progress-bar" style={{ width: individualSkillsEntries[i][1] }}>{individualSkillsEntries[i][1]}</div>
                </div>
            </div>
        </div>

        individualSkills.push(individualSkill);
    }


    return individualSkills;

}

export default SkillsInIndividualField;