import SkillsInIndividualField from "./SkillsInIndividualField";

function SkillsTitle(props){

    let skillsInIndividualField = props.skills;

    return (
        <div class="row p-5">
            <div class="col-12 col-md-10 offset-md-2">
                <h3 class="font-weight-bold">{props.title}</h3>
                <hr />

                <SkillsInIndividualField skills={props.skills} />
            </div>
        </div>
    )
}

export default SkillsTitle;