import Points from "./Points";

function SubPointElements(props) {

    let points = <Points subPoints={props.subPoints} />;

    if (props.experience != null) {
        return (<div className={"row p-5 " + props.bg} >
            <div className="col-12 col-md-10 offset-md-2">
                <h3 class="font-weight-bold">{props.experience.designation}</h3>
                <hr />
                <h4>{props.experience.company} / {props.experience.duration}</h4>
                <br />
                {points}
            </div>
        </div>);
    } else {
        return (
            <div className={"row p-5 " + props.bg} >
                <div className="col-12 col-md-10 offset-md-2">
                    {points}
                </div>
            </div>
        );
    }
}

export default SubPointElements;