function SubTitleElement(props) {

  return (
      <div className={"row " + props.bg} id={props.subTitle}>
        <div className="col-9 col-md-6 col-lg-3 mt-n4">
          <div className="card shadow">
            <div className="card-body">
              <div className="font-weight-bold text-muted">{props.subTitle}</div>
            </div>
          </div>
        </div>
      </div>
  );

}

export default SubTitleElement;