function Navbar(props) {
    let stickyTop = "";
    if (props.stickyTop)
        stickyTop = "sticky-top ";

    return (
        <nav id="easy-navigation" class={"row navbar navbar-expand-md navbar-light bg-light " + stickyTop}>
            <a class="navbar-brand" href="#"></a>
            <button class="navbar-toggler float-right" type="button" data-toggle="collapse" data-target="#contentNavigationList" aria-controls="contentNavigationList" aria-expanded="false" aria-label="Toggle navigation">
                <span class="navbar-toggler-icon"></span>
            </button>
            <div class="collapse navbar-collapse justify-content-end" id="contentNavigationList">
                <ul class="nav navbar-nav">
                    {props.navList.map(navigation => (
                        <li class="nav-item">
                            <a class="nav-link text-info" href={navigation.url}>{navigation.name}</a>
                        </li>
                    ))}
                </ul>
            </div>
        </nav>
    );
}

export default Navbar;